import { Table }          from "reactstrap";
import { useFormContext } from "react-hook-form";

import TdInput from "Components/Appraisal/TdInput";

const TableSources = ({ index }) => {

	const hookForm = useFormContext();

	return (
		<>
			<h5>Fuentes</h5>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th className="text-center">Nombre</th>
						<th className="text-center">Teléfono</th>
						<th className="text-center">Fecha</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.name`} type="text" />
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.phone`} inpt_type="isNotMasked" />
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.date`}  inpt_type="date" />
					</tr>
				</tbody>
			</Table>
		</>
	);
};

export default TableSources;
