import { useFormContext } from "react-hook-form";

import Form                             from "Components/Appraisal/Form";
import { CheckboxInpt, FormInputGroup } from "Components/Global";

const DeclarationsForm = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<h5>Declaraciones:</h5>
			<p className="m-2 ">
				Las declaraciones de hechos contenidas en el presente estudio son verdaderas y correctas.
				No tenemos interés presente o futuro en la propiedad que es objeto de este avalúo, no tenemos interés personal o parcial con respecto a las partes involucradas; además declaramos que no participamos en el capital o en los órganos administrativos del promovente y manifestamos completa independencia con la propiedad de los bienes.
				Los emolumentos relativos al desarrollo del trabajo valuatorio, no están condicionados al reporte de un valor predeterminado o dirigido hacia un valor que favorezca la causa de un cliente.
			</p>
			<CheckboxInpt
				name="statement_warning.cus_one"
				control={hookForm.control}
				label="En virtud de que no se conoce la superficie de terreno de los comparables que se tienen en oferta, por tratarse de inmuebles bajo régimen en condominio, se considera un factor para el C.U.S. de 1.00."
				rules={{ required : false }}
			/>
			<CheckboxInpt
				name="statement_warning.undivided_supposed"
				control={hookForm.control}
				label="Debido a que se desconoce el indiviso y/o la superficie de terreno del conjunto, se considera la superficie registrada en la boleta predial con un indiviso del 100%."
				rules={{ required : false }}
			/>
			<FormInputGroup
				bsSize="sm"
				type="textarea"
				className="mt-1"
				label="Completar (Opcional):"
				name="statement_warning.to_complete"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<hr />
			<h5>Consideraciones:</h5>
			<FormInputGroup
				bsSize="sm"
				type="textarea"
				label="Consideraciones Previas al Avalúo:"
				name="statement_warning.considerations"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<hr />
			<h5>Advertencias:</h5>
			<FormInputGroup
				bsSize="sm"
				type="textarea"
				label="Advertencia:"
				name="statement_warning.nomenclature"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
		</Form>
	);
};

export default DeclarationsForm;
