import React             from "react";
import { AlertTriangle } from "react-feather";
const CostMessagge = () => {
	return (
		<>
			<AlertTriangle style={{ color : "#ff9f43"}} />
			<p className="mt-1">
				El costo del avalúo no puede ser calculado, ya que no se han definido la superficie ó el valor comercial.
				Concluya primero el avalúo y posteriormente entre a esta sección para definir el costo.
			</p>
		</>
	);
};

export default CostMessagge;
