import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { getAverage } from "Helpers/formulas";

const useColumnAverage = ({ arrayKey, fieldKey, length = 0, name, isAbsAverage  }) => {

	const { setValue } = useFormContext();

	const vals = useWatch({ name : [...Array(length).keys()].map((index) => `${arrayKey}.${index}.${fieldKey}`)});

	useEffect(() => {
		const average = getAverage(vals);

		const result = isAbsAverage ? Math.abs(average) : average;

		setValue(name, result);
	}, [vals]);

	return vals;

};

export default useColumnAverage;
