import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";

const funcionalityOptions = [
	{
		value : 0,
		label : "Las características de la construcción permiten el desarrollo óptimo de habitantes",
	},
	{
		value : 1,
		label : "Las características de la construcción permiten el desarrollo suficiente de los habitantes",
	},
	{
		value : 2,
		label : "Las características de la construcción permiten el desarrollo insuficiente de los habitantes",
	},
];

const conservationStateOptions = [
	{
		value : 0,
		label : " Presenta condiciones óptimas de conservación",
	},
	{
		value : 1,
		label : " Presenta condiciones suficientes de conservación",
	},
	{
		value : 2,
		label : " Presenta condiciones insuficientes de conservación",
	},
];

const lifespanOptions = [
	{
		value : 0,
		label : " Con una vida útil estimada en más de 40 años",
	},
	{
		value : 1,
		label : " Con una vida útil estimada entre los 30 y 40 años",
	},
	{
		value : 2,
		label : " Con una vida estimada en menos de 30 años",
	},
];


const Quality = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="funcionality"
				label="Funcionalidad:"
				hookForm={hookForm}
				options={funcionalityOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="conservation_state"
				label="Estado de Conservación:"
				hookForm={hookForm}
				options={conservationStateOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="lifespan"
				label="Vida Útil Remanente:"
				hookForm={hookForm}
				options={lifespanOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default Quality;
